"use client";

import { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { Search, Loader2 } from 'lucide-react';
import { DashboardLayout } from '@/components/layout/DashboardLayout';
import { ProjectTable, ProjectTableSkeleton } from '@/components/projects/ProjectTable';
import { AddProjectDialog } from '@/components/projects/AddProjectDialog';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';

export default function ProjectsPage() {
  const router = useRouter();
  
  // Data State
  const [projects, setProjects] = useState([]);
  const [departments, setDepartments] = useState([]);
  const [loading, setLoading] = useState(true);

  // Filter State
  const [search, setSearch] = useState('');
  const [deptFilter, setDeptFilter] = useState('all');
  const [statusFilter, setStatusFilter] = useState('all');
  const [yearFilter, setYearFilter] = useState('all');

  // Fetch Projects with Filters
  const fetchProjects = useCallback(async () => {
    setLoading(true);
    try {
      const params = new URLSearchParams();
      if (search) params.append('search', search);
      if (deptFilter !== 'all') params.append('departmentId', deptFilter);
      if (statusFilter !== 'all') params.append('status', statusFilter);
      if (yearFilter !== 'all') params.append('year', yearFilter);

      const res = await fetch(`/api/projects?${params.toString()}`);
      if (!res.ok) throw new Error('Failed to fetch');
      const data = await res.json();
      setProjects(data);
    } catch (error) {
      console.error("Error fetching projects:", error);
    } finally {
      setLoading(false);
    }
  }, [search, deptFilter, statusFilter, yearFilter]);

  // Fetch Departments for Filter Dropdown
  const fetchDepartments = useCallback(async () => {
    try {
      const res = await fetch('/api/departments');
      if (res.ok) {
        const data = await res.json();
        setDepartments(data);
      }
    } catch (error) {
      console.error("Error fetching departments:", error);
    }
  }, []);

  // Initial Load
  useEffect(() => {
    fetchDepartments();
  }, [fetchDepartments]);

  // Debounced Project Fetching
  useEffect(() => {
    const timer = setTimeout(() => {
      fetchProjects();
    }, 400); // 400ms debounce to prevent spamming API on every keystroke
    return () => clearTimeout(timer);
  }, [fetchProjects]);

  return (
    <DashboardLayout 
      title="Projects Management" 
      currentPath="/projects" 
      onNavigate={(path) => router.push(path)}
    >
      <div className="space-y-6">
        {/* Top Header Section */}
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
          <div>
            <h1 className="text-2xl font-bold tracking-tight">Projects</h1>
            <p className="text-muted-foreground text-sm">
              Manage and track all company billing projects.
            </p>
          </div>
          <AddProjectDialog onProjectAdded={fetchProjects} />
        </div>

        {/* Filter Bar */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-5 gap-3 p-4 glass-card rounded-xl border border-border/50">
          {/* Search Input */}
          <div className="relative lg:col-span-2">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="Search by project or client..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-9 bg-secondary/30"
            />
          </div>

          {/* Department Filter */}
          <Select value={deptFilter} onValueChange={setDeptFilter}>
            <SelectTrigger className="bg-secondary/30">
              <SelectValue placeholder="Department" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Departments</SelectItem>
              {departments.map((dept: any) => (
                <SelectItem key={dept.id} value={dept.id.toString()}>
                  {dept.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          {/* Status Filter */}
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="bg-secondary/30">
              <SelectValue placeholder="Status" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Status</SelectItem>
              <SelectItem value="ONGOING">Ongoing</SelectItem>
              <SelectItem value="COMPLETED">Completed</SelectItem>
              <SelectItem value="FUTURE">Future</SelectItem>
            </SelectContent>
          </Select>

          {/* Year Filter */}
          <Select value={yearFilter} onValueChange={setYearFilter}>
            <SelectTrigger className="bg-secondary/30">
              <SelectValue placeholder="Year" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Years</SelectItem>
              {[2023, 2024, 2025, 2026].map((year) => (
                <SelectItem key={year} value={year.toString()}>
                  {year}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {/* Projects Table Content */}
        {loading ? (
          <ProjectTableSkeleton />
        ) : (
          <ProjectTable
            projects={projects.map((p: any) => ({
              ...p,
              name: p.projectName,
              clientName: p.client?.name || 'Unknown',
              signDate: p.startDate,
              totalBudget: Number(p.totalProjectValue),
              // Determine status if not explicitly in root object
              status: p.status || (p.bills.every((b: any) => b.status === 'PAID') ? 'COMPLETED' : 'ONGOING')
            }))}
            onProjectClick={(project) => router.push(`/projects/${project.id}`)}
          />
        )}
      </div>
    </DashboardLayout>
  );
}