"use client";

import { useState, useEffect } from "react";
import { useForm, useFieldArray } from "react-hook-form";
import { Plus, Trash2, Loader2, Building2 } from "lucide-react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { toast } from "sonner";

export function AddProjectDialog({ onProjectAdded }: { onProjectAdded: () => void }) {
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [clients, setClients] = useState([]);
  const [categories, setCategories] = useState([]);
  const [departments, setDepartments] = useState([]);
  const [isNewClient, setIsNewClient] = useState(false);

  const { register, control, handleSubmit, reset, watch, setValue } = useForm({
    defaultValues: {
      projectName: "",
      clientId: "",
      newClient: { name: "", contactPerson: "" },
      categoryId: "",
      departmentId: "",
      totalProjectValue: "",
      startDate: "",
      bills: [{ billName: "Down Payment", billPercent: "20", billAmount: "" }]
    }
  });

  const { fields, append, remove } = useFieldArray({ control, name: "bills" });
  const totalValue = watch("totalProjectValue");

  useEffect(() => {
    if (open) {
      Promise.all([
        fetch('/api/clients').then(res => res.json()),
        fetch('/api/categories').then(res => res.json()),
        fetch('/api/departments').then(res => res.json())
      ]).then(([cl, cat, dept]) => {
        setClients(cl);
        setCategories(cat);
        setDepartments(dept);
      });
    }
  }, [open]);

  const onSubmit = async (data: any) => {
    setLoading(true);
    try {
      const res = await fetch('/api/projects', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
      });
      if (!res.ok) throw new Error("Failed to save");
      toast.success("Project created successfully");
      setOpen(false);
      reset();
      onProjectAdded();
    } catch (err) {
      toast.error("Error creating project");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button className="gap-2"><Plus className="w-4 h-4" /> Add New Project</Button>
      </DialogTrigger>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader><DialogTitle>Create New Project</DialogTitle></DialogHeader>
        
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Project Name</Label>
              <Input {...register("projectName")} required />
            </div>
            <div className="space-y-2">
              <Label>Total Value (BDT)</Label>
              <Input type="number" {...register("totalProjectValue")} required />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Client</Label>
              <Select onValueChange={(v) => {
                if(v === "new") setIsNewClient(true);
                else { setIsNewClient(false); setValue("clientId", v); }
              }}>
                <SelectTrigger><SelectValue placeholder="Select Client" /></SelectTrigger>
                <SelectContent>
                  <SelectItem value="new" className="text-primary font-bold">+ Add New Client</SelectItem>
                  {clients.map((c: any) => <SelectItem key={c.id} value={c.id.toString()}>{c.name}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label>Department</Label>
              <Select onValueChange={(v) => setValue("departmentId", v)}>
                <SelectTrigger><SelectValue placeholder="Department" /></SelectTrigger>
                <SelectContent>
                  {departments.map((d: any) => <SelectItem key={d.id} value={d.id.toString()}>{d.name}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>
          </div>

          {isNewClient && (
            <div className="p-4 bg-primary/5 border border-primary/20 rounded-lg space-y-4">
              <p className="text-sm font-bold flex items-center gap-2"><Building2 className="w-4 h-4" /> New Client Details</p>
              <div className="grid grid-cols-2 gap-4">
                <Input placeholder="Client Name" {...register("newClient.name")} required />
                <Input placeholder="Contact Person" {...register("newClient.contactPerson")} />
              </div>
            </div>
          )}

          <div className="space-y-4 border-t pt-4">
            <div className="flex justify-between items-center">
              <Label className="text-lg font-bold">Billing Phases</Label>
              <Button type="button" variant="outline" size="sm" onClick={() => append({ billName: "", billPercent: "", billAmount: "" })}>
                <Plus className="w-4 h-4 mr-2" /> Add Phase
              </Button>
            </div>
            
            {fields.map((field, index) => (
              <div key={field.id} className="grid grid-cols-12 gap-3 items-end bg-muted/30 p-3 rounded-lg">
                <div className="col-span-5 space-y-1">
                  <Label className="text-xs">Phase/Bill Name</Label>
                  <Input {...register(`bills.${index}.billName` as const)} placeholder="e.g. Design Completion" required />
                </div>
                <div className="col-span-2 space-y-1">
                  <Label className="text-xs">%</Label>
                  <Input type="number" {...register(`bills.${index}.billPercent` as const)} onChange={(e) => {
                    const pct = parseFloat(e.target.value);
                    if(pct && totalValue) setValue(`bills.${index}.billAmount` as any, (pct/100 * parseFloat(totalValue)).toString());
                  }} />
                </div>
                <div className="col-span-4 space-y-1">
                  <Label className="text-xs">Amount</Label>
                  <Input type="number" {...register(`bills.${index}.billAmount` as const)} required />
                </div>
                <div className="col-span-1">
                  <Button type="button" variant="ghost" size="icon" className="text-destructive" onClick={() => remove(index)}>
                    <Trash2 className="w-4 h-4" />
                  </Button>
                </div>
              </div>
            ))}
          </div>

          <Button type="submit" className="w-full" disabled={loading}>
            {loading ? <Loader2 className="w-4 h-4 animate-spin mr-2" /> : "Create Project"}
          </Button>
        </form>
      </DialogContent>
    </Dialog>
  );
}